<template>
  <div
    class="permission-wrapper"
    :class="{ empty: !hasProject && !hasContract }"
  >
    <app-header title="权限申请" />
    <div class="action-list" v-if="hasProject || hasContract">
      <cell
        v-if="hasProject"
        :border="hasContract"
        title="项目管理"
        @click="$router.push('/user/project-manage')"
      ></cell>
      <cell
        v-if="hasContract"
        title="合同管理"
        @click="$router.push('/user/contract-manage')"
      ></cell>
    </div>
    <empty-page
      emptyMsg="暂无权限"
      :showEmpty="!hasProject && !hasContract"
      :loadingPage="this.loading"
      v-if="!loading"
    />
  </div>
</template>

<script>
import Cell from '@components/app-cell';
import { getBaseInfo } from '@/src/api/user-state';

export default {
  components: {
    Cell,
  },
  data() {
    return {
      hasProject: false,
      hasContract: false,
      loading: false,
    };
  },
  activated() {
    this.hasProject = false;
    this.hasContract = false;
    this.loading = true;
    getBaseInfo().then(res => {
      this.loading = false;
      let functionList = res.function_list;
      functionList.forEach(v => {
        if (v.code === 'apply_proj') {
          this.hasProject = true;
        }
        if (v.code === 'apply_contract') {
          this.hasContract = true;
        }
      });
    });
  },
};
</script>

<style lang="scss" scoped>
.action-list {
  padding-top: 10px;
}
.permission-wrapper {
  &.empty {
    height: 100%;
    background: #fff;
    overflow: hidden;
  }
}
</style>
